<template>
  <div class="message-alert">
    <div class="bg-white rounded-15 border border-gray2 py-3 px-5 mb-2 text-main-dark2"
      v-for="(item, i) in messages" :key="i">
      <div class="d-flex align-items-center">
        <h6 class="mb-0 me-auto" style="font-size: 14px;">系統提示 :</h6>
        <a href="#" @click.prevent="removeMessage(i)">
          <img width="12" height="12" src="@/assets/stylesheets/icon/X-01.png" alt="">
        </a>
    </div>
    <p class="mb-0">{{ item.message }}</p>
    </div>
  </div>
</template>


<script>
import { inject, toRefs } from 'vue';

export default {
  name: 'AlertMessage',
  setup() {
    const alertMessage = inject('alertMessage');
    const { messageState } = alertMessage;

    const removeMessage = (num) => {
      messageState.messages.splice(num , 1);
    }

    return {
      ...toRefs(messageState),
      removeMessage,
    }
  }
};
</script>

<style lang="scss" scoped>
.message-alert {
	position: fixed;
	width: 300px;
	z-index: 1100;
	top: 20px;
	right: 20px;
}
</style>